}

.bold-font {
   @include font-type($font-Proxima-regular, 16px, 900);
}

.mt15 {
   margin-top: 15px;
}

.mt25 {
   margin-top: 25px;
}

.mb15 {
   margin-bottom: 15px;
}

.test {
   padding: 15px;
   @include font-type($font-Proxima-regular, 14px, 400);
}

.custom-select-box__control {
   border-radius: 0 !important;
   box-shadow: none !important;
   background: #fff !important;
   border: 1px solid #d0d0d0 !important;
   padding: 5px 15px !important;
   font-size: 16px !important;
   line-height: 26px !important;
   color: #4D5055 !important;
}

.custom-select-box__control--is-focused {
   border: 1px solid #4d9bdd !important;
   cursor: pointer !important;
}

.custom-select-box__value-container {
   padding: 0px !important;
}

.custom-select-box__indicator-separator {
   display: none !important;
}

.custom-select-box__menu {
   margin-top: -1px !important;
   border-radius: 0px !important;
   border: 1px solid #4d9bdd !important;
   border-top: 1px solid #f1f1f1 !important;
   box-shadow: none !important;
}

.custom-select-box__indicator {
   color: #4d9bdd !important;
}

.custom-select-box__option {
   color: #808080 !important;
   font-size: 16px !important;
   padding: 12px 15px !important;
}

.custom-select-box__option--is-selected {
   background-color: #f8f8f8 !important;
   color: #4d9bdd !important;
}

.custom-select-box__option--is-focused {
   background-color: #f8f8f8 !important;
   color: #4d9bdd !important
}
.custom-select-box__value-container--is-multi{
   height: 26px!important;
}
.test {}

.select-checkbox {
   display: none;
}

.select-checkbox-label {
   /* Style for checkbox normal */
   width: 16px;
   height: 16px;
   display: block;
   background: rgba(0, 0, 0, 0.2);
   border-radius: 2px;
   position: relative;
}

.select-checkbox-label>span {
   display: inline-block;
   padding-left: 25px;
   margin-top: -4px;
   position: absolute;
}

.select-checkbox-label.checked {
   background: #4d9bdd;
}

.select-checkbox-label.checked:after {
   content: '';
   position: absolute;
   left: 4px;
   top: 7px;
   background: white;
   width: 2px;
   height: 2px;
   box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
   transform: rotate(45deg);
}

.search-wrapper {
   border: 1px solid $primary-border-color;
   margin: 20px;
   padding: 15px 20px;
   & .tag-name-container {
      display: inline-block;
      margin-right: 15px;
      background: #ccc;
      color: white;
      padding: 5px 10px;
   }
   & .search-result-wrapper {
      list-style-type: none;
      padding: 0px;
      border: 1px solid $primary-border-color;
      & li {
         color: #808080;
         font-size: 16px;
         padding: 12px 15px;
         &:hover {
            background-color: #f8f8f8;
            color: #4d9bdd;
            cursor: pointer;
            & label {
               cursor: pointer;
            }
         }
      }
   }
}

// css for label
.custom-label {
   position: relative;
   margin-bottom: 10px;
   @include font-type($font-Proxima-regular, 14px, 900);
   color: #666a73;
}

// css for textox
input[type="text"],
input[type="email"],
input[type="number"],
input[type="password"],
textarea {
   width: 100%;
   min-height: 46px;
   box-sizing: border-box;
   border: 1px solid #d0d0d0;
   background: #fff;
   padding: 11px 14px;
   font-size: 16px;
   word-break: normal;
   outline: none;
   -webkit-appearance: none;
   border-radius: 0;
   line-height: normal;
   margin: 0;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
textarea:focus {
   border-color: #4db7fc;
}

button:focus {
   outline: none;
}

button:active,
button .active,
.button:active,
.button .active,
a.button:active,
a.button .active,
a.button:visited:active,
a.button:visited .active,
a.button:link:active,
a.button:link .active {
   opacity: 0.6;
}

//css for button
%btn-main {
   -webkit-appearance: none;
   font-weight: 100;
   text-align: center;
   text-decoration: none;
   line-height: 20px;
   cursor: pointer;
   outline: none;
   padding: 12px 12px 11px;
   border-radius: 0;
   display: inline-block;
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
   box-sizing: border-box;
}

.btn-custom-primary {
   @extend %btn-main;
   background-color: #00a2ff;
   -webkit-appearance: none;
   border: 1px solid #00a2ff;
   color: #fff;
}

.btn-custom-secondary {
   @extend %btn-main;
   background-color: transparent;
   border: 1px solid #b8b298;
   color: #b8b298;
}

// css for toggle switch
.switch-wrapper.on {
   color: #14c6a2;
   border: 1px solid #00c19a;
   background-color: #fff;
}

.switch-wrapper.off {
   color: #fff;
   border: 1px solid #a8a8a8;
   background-color: #a8a8a8;
   position: relative;
}

.switch-wrapper.off .is.on {
   left: -100%;
}

.switch-wrapper.off .is.off {
   margin-right: -6px;
}

.switch-wrapper {
   cursor: pointer;
   display: inline-block;
   overflow: hidden;
   position: relative;
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
   font-size: 14px;
   text-transform: uppercase;
   line-height: 1;
   word-wrap: normal;
   word-break: normal;
}

.switch-wrapper.on .is.on {
   left: 0;
}

.switch-wrapper .is {
   margin: 0 2px;
   padding: 9px 30px 7px 10px;
   position: relative;
   width: 100%;
   box-sizing: border-box;
   transition: left 0.2s linear, margin 0.2s linear;
}

.switch-wrapper.on .is.off {
   margin-right: 26px;
}

.switch-wrapper .is.off {
   right: -99%;
   padding-left: 25px;
   position: absolute;
   top: 0;
}

.switch-wrapper .is.off:before {
   position: absolute;
   left: 0;
   top: 6px;
   content: '';
   display: inline-block;
   width: 18px;
   height: 18px;
   border-radius: 50%;
}

.switch-wrapper.on .is:before {
   background: #00c19a;
}

.switch-wrapper.off .is:before {
   background: #fff;
}